<template>
  <header>
    <h1>TODO it!</h1>
    <p align="right">
      <!-- {{ authname }} -->
      <span  id="logout">
        <b-badge pill @click="getLocation" v-bind:variant="thema">Weather</b-badge>
        <b-badge pill @click="logout" v-bind:variant="thema">Log Out</b-badge>
      </span>
    </p>
    <div id="current">
      <b-button id="day" v-bind:variant="reversethema">Today is.. {{ printDate() }}</b-button>
    </div>
    <br><br>
    <i id="weatherInfo" v-if="weather.name">
      <img v-bind:src="imageUrl"/>
      {{ weather.name }}
      {{(weather.main.temp - 273.15).toFixed(2)}}°C
      {{ weather.weather[0].description }}
    </i>
  </header>
</template>
<script>
import { mapGetters } from 'vuex';
import axios from 'axios';

export default {
  data(){
    return{
      weather: {name:'', main:{temp:''}, weather:[{main:'', description:''}]},
      imageUrl: '',
      geoLocation:{lat:0, lng:0},
    }
  },
  methods:{
    printDate(){
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth()+1
      var day = date.getDate();
      if(month < 10){
          month = "0" + month;
      }
      if(day < 10){
          day = "0" + day;
      }
      var today = year+"년 "+month+"월 "+day+"일 ";
      return today;
    },
    logout(){
      this.$store.dispatch('logout');
    },
    getLocation(){
      var options={
        enableHighAccuracy: false, //defaults to false
        timeout: Infinity, //defaults to Infinity
        maximumAge: 0 //defaults to 0
      }
      let temp = this;

        this.$getLocation(options)
        .then(coordinates => {
          temp.geoLocation.lat = coordinates.lat;
          temp.geoLocation.lng = coordinates.lng;

            temp.getWeather();

        });
        console.log(JSON.stringify(this.weather));
    },
    getWeather(){
       var apiKey = "2553feadd1d8eb56f5cf7e2e205e8af6";

       let temp = this;
       var apiURI = "https://api.openweathermap.org/data/2.5/weather?lat="+JSON.stringify(this.geoLocation.lat)+"&lon="+JSON.stringify(this.geoLocation.lng)+"&appid="+apiKey;
           $.ajax({
               url: apiURI,
               dataType: "json",
               type: "GET",
               async: "false",
               success: function(resp) {
                 temp.weather = resp;
                 temp.imageUrl = "https://openweathermap.org/img/w/"+ temp.weather.weather[0].icon +".png";
               }
           })
    },
  },
  created(){
  },
  computed: {
    reversethema(){
      return this.$store.getters.getreverseThema;
    },
    authname(){ //route로 추가
      return this.$store.getters.getAuth.name;
    },
    thema(){
      return this.$store.getters.getThema;
    },
  },
}

</script>

<style scoped>
  h1 {
    color: #2F3B52;
    font-weight: 900;
    margin: 2.5rem 0 1.5rem;
  }
  #current{
    margin: auto;
    padding: 1px;

    height: 50px;
    width:100%;
    line-height: 20px;
    border-radius: 5px;
    float:center;
  }
  #day{
    line-height:40px;
    text-align:center;
    font-size:calc(20px + 1vw);
  }
  #logout{
    width:6em;
    height:3em;
    color: #FFFFFF;
    border-radius: 7px;
    font-size:calc(10px + 0.8vw);
    text-align:left;
    float:center;
  }
  #weatherInfo{
    width:220px;
    height:50px;
    background-color:#377bb3;
    border-radius:15px;
    padding:3px;
    text-align:center;
    align-content: center;
    -webkit-text-fill-color:white;
  }
</style>



